          value: "",
        },
        gettime: {
          key: "4.疼痛起病时间",
          value: "",
        },
        more: {
          key: "5.有无麻木症状",
          value: "",
        },
        gettimes: {
          key: "6.麻木起病时间",
          value: "",
        },
        smell: {
          key: "7.是否有抽搐症状",
          value: "",
        },
        age: {
          key: "8.抽搐起病年龄",
          value: "",
        },
        bloodcolor: {
